<template>
        <div class="webinar-plannedWebinars">
            <div class="webinar-plannedWebinars-title">
                <p>Запланированные вебинары</p>
            </div>
                
                
            <div class="webinar-plannedWebinars-table">
                
                <div class="webinar-plannedWebinars-table-header">
                    <p></p>
                    <p>ДАТА</p>
                    <p>НАЗВАНИЕ / ОПИСАНИЕ</p>
                    <p>ЦЕНА</p>
                    <p>ТАРИФ</p>
                </div>
                
                
                <div v-if="isPlannedWebinars" class="webinar-plannedWebinars-table-body">
                    <PlannedWebinarsElement />
                    <PlannedWebinarsElement />
                </div>

                <div v-else class="webinar-plannedWebinars-table-body">
                    <WebinarsIsNull panel="planned"/>
                </div>
            </div> 
            
        </div>
</template>

<script>  
    import PlannedWebinarsElement from './components/PlannedWebinarsElement.vue';
    import WebinarsIsNull from './components/WebinarsIsNull.vue';
    
    export default {
        name: 'PlannedWebinars',
        components: {
            PlannedWebinarsElement, WebinarsIsNull,
        },
        data() {
            return {
                isPlannedWebinars: true,
            }
        },
    }
</script>

<style>
    .webinar-plannedWebinars {
        margin-top: 29px;
        height: auto;
        
        display: flex;
        flex-direction: column;
        font-weight: 600;
        
        margin-bottom: 28px;
    }
    
    .webinar-plannedWebinars-title {
        width: 100%;
        height: 29%;
        
        display: flex;
        align-items: center;
        color: #30353E;
        
        font-size: 18px;
        font-family: 'Montserrat', sans-serif;
        color: #30353E;
    }
    
    .webinar-plannedWebinars-table {
        width: 100%;
        height: 71%;
    }
    
    .webinar-plannedWebinars-table-header {
        width: 100%;
        height: 23.1%;
        margin: 10px 0;
        
        font-family: 'Montserrat', sans-serif;
        font-size: 10px;
        color: #8F8F8F;
        
        display: flex;  
        align-items: center;
        flex-direction: row;
    }
    
    .webinar-plannedWebinars-table-body {
        width: 100%;
        height: 76.9%;
    }
    
    .webinar-plannedWebinars-table-body div:first-child {
        margin-top: 0;
    }
    
    
    .webinar-plannedWebinars-table-header p:nth-child(1) {
        width: 12.55%;
    }
    
    .webinar-plannedWebinars-table-header p:nth-child(2) {
        width: 12.90%;
        padding-left: 1.2%;
    }
    
    .webinar-plannedWebinars-table-header p:nth-child(3) {
        width: 35.79%;
    }
    
    .webinar-plannedWebinars-table-header p:nth-child(4) {
        width: 21.62%;
    }
    
    .webinar-plannedWebinars-table-header p:nth-child(5) {
        width: 15.94%;
    }
    

</style>



