<template>
        <div class="pastWebinars-table-body-element">
            <div class="pastWebinars-table-body-element-video">
                <div class="pastWebinars-table-body-element-video-icon">
                    <img src="../../../assets/webinar/play-button.svg" alt="icon" />
                </div>
                <div class="pastWebinars-table-body-video-content">

                </div>
            </div>

            <div class="pastWebinars-table-body-element-date">
                <p>12.10.19-<br/>13.10.19</p>
            </div>

            <div class="pastWebinars-table-body-element-name">
                <p>Семинар «Подробный ортопедический протокол»</p>
                <p>1 занятие</p>
            </div>

            <div class="pastWebinars-table-body-element-tickets">
                <p>-</p>
            </div>
            <div class="pastWebinars-table-body-element-maximumViewers">
                <p>-</p>
            </div>
            <div class="pastWebinars-table-body-element-totalViews">
                <p>-</p>
            </div>
            <div class="pastWebinars-table-body-element-shelfLife">
                <p>-</p>
            </div>
        </div>
</template>

<script>
    export default {
        name: 'PastWebinarsElement',
    }
</script>

<style>
    .pastWebinars-table-body-element {
        width: 100%;
        height: 70px;
        font-family: 'Montserrat', sans-serif;
        margin-top: 10px;
        
        display: flex;
        flex-direction: row;
    }
    
    .pastWebinars-table-body-element-video {
        width: 11.35%;
        height: 100%;
        margin-right: 1.2%;
        position: relative;
        
        display: flex;
        justify-content: center;
        align-items: center;
    }
    
    .pastWebinars-table-body-element-video:hover {
        cursor: pointer;
    }
    
    .pastWebinars-table-body-element-video-icon {
        width: 35px;
        height: 35px;
        position: absolute;
    }
    
    .pastWebinars-table-body-video-content {
        width: 100%;
        height: 100%;
        border-radius: 5px;
        background: grey;
    }
    
    .pastWebinars-table-body-element-date {
        width: 6.84%;
        padding-left: 1.2%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        background-color: white;
        border-radius: 5px 0 0 5px;
    }
    
    .pastWebinars-table-body-element-date p:first-child {
        font-size: 12px;
        color: #333333;
    }
    
    .pastWebinars-table-body-element-name {
        width: 30.26%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        background-color: white;
    }
    
    .pastWebinars-table-body-element-name p:first-child {
        font-size: 12px;
        color: #333333;
    }
    
    .pastWebinars-table-body-element-name p:last-child {
        margin-top: 5px;
        color: #999999;
        font-size: 12px;
    }
    
    .pastWebinars-table-body-element-tickets {
