    .pastWebinars-table-body-element-tickets p:first-child {
        font-size: 12px;
        color: #333333;
    }
    
    .pastWebinars-table-body-element-maximumViewers {
        width: 14.24%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        background-color: white;
    }
    
    .pastWebinars-table-body-element-maximumViewers p:first-child {
        font-size: 12px;
        color: #333333;
    }
    
    .pastWebinars-table-body-element-totalViews {
        width: 13.5%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        background-color: white;
    }
    
    .pastWebinars-table-body-element-totalViews p:first-child {
        font-size: 12px;
        color: #333333;
    }
    
    .pastWebinars-table-body-element-shelfLife {
        width: 11.52%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        background-color: white;
        border-radius: 0 5px 5px 0;
    }
    
    .pastWebinars-table-body-element-shelfLife p:first-child {
        font-size: 12px;
        color: #333333;
    }

</style>



