<template>
    <div class="webinar-webinarWindow">
        <div class="webinar-webinarWindow-webinar">
            
            <div @click="gotoCreate_Webinar()" class="webinar-webinarWindow-webinar-container">
                <div class="webinar-webinarWindow-webinar-container-center">
                    <div class="webinar-webinarWindow-webinar-container-center-icon">
                        <img src="../../assets/webinar/create_webinar.svg" alt="icon" />
                    </div>
                    <p>Создать вебинар</p>
                </div>
            </div>
            
        </div>
        <div class="webinar-webinarWindow-statistics">
            <div class="webinar-webinarWindow-statistics-container">
                <div class="webinar-webinarWindow-statistics-container-header">
                    <p>Статистика</p>
                </div>
                <div class="webinar-webinarWindow-statistics-container-text">
                    <div class="webinar-webinarWindow-statistics-container-text-left">
                        <p>Статус</p>
                        <p>Битрейт</p>
                        <p>Разрешение</p>
                    </div>
                    <div class="webinar-webinarWindow-statistics-container-text-right">
                        <p>-</p>
                        <p>-</p>
                        <p>-</p>
                    </div>
                </div>
                <div class="webinar-webinarWindow-statistics-container-line">
                    <div class="webinar-webinarWindow-statistics-container-line-visual"></div>
                </div>
                <div class="webinar-webinarWindow-statistics-container-text">
                    <div class="webinar-webinarWindow-statistics-container-text-left">
                        <p>Максимум зрителей</p>
                        <p>Сейчас смотрят</p>
                        <p>Всего просмотров</p>
                    </div>
                    <div class="webinar-webinarWindow-statistics-container-text-right">
                        <p>0</p>
                        <p>0</p>
                        <p>0</p>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: 'WebinarWindow',
        methods: {
            gotoCreate_Webinar() {
                this.$router.push({name: "create_webinar"});
            }
        }
    }
</script>

<style>
    .webinar-webinarWindow {
        height: 294px;
        background: white;
        border-radius: 9px;
        
        display: flex;
        flex-direction: row;
        
        margin-top: 18px;
    }
    
    .webinar-webinarWindow-webinar, .webinar-webinarWindow-statistics {
        height: 100%;
        width: 50%;
        
        display: flex;
        justify-content: center;
        align-items: center;
    }
    
    .webinar-webinarWindow-webinar-container {
        width: 86%;
        height: 73.1%;
        
        display: flex;
        justify-content: center;
        align-items: center;
        
        border: 4px dashed #D9E7FC;
        border-radius: 20px;
    }
    
    .webinar-webinarWindow-webinar-container:hover {
        cursor: pointer;
    }
    
    .webinar-webinarWindow-webinar-container-center {
        width: 40%;
        height: 50%;
        
        font-size: 16px;
        font-family: 'Montserrat', sans-serif;
        color: #C0D1F2;
        
        display: flex;
        justify-content: space-around;
        align-items: center;
        flex-direction: column;
    }
    
    .webinar-webinarWindow-webinar-container-center-icon {
        width: 64px;
        height: 64px;
    }
    
    .webinar-webinarWindow-statistics-container {
        width: 80%;
        height: 78.9%;
        color: #30353E;
    }
    
    .webinar-webinarWindow-statistics-container-header {
        height: 16.4%;
        width: 100%;
        
        display: flex;
        align-items: center;
        
        font-family: 'Montserrat', sans-serif;
        font-weight: bold;
        font-size: 20px;
    }
    
    .webinar-webinarWindow-statistics-container-text {
        height: 38.4%;
        width: 100%;
        font-size: 14px;
        font-family: 'Montserrat', sans-serif;
        
        display: flex;
        flex-direction: row;
    }
    
    .webinar-webinarWindow-statistics-container-line {
        height: 6.8%;
        width: 100%;
        
        display: flex;
        align-items: center;
    }
    
    .webinar-webinarWindow-statistics-container-line-visual {
        height: 1px;
        width: 100%;
        background: #E1E1E1;
    }
    
    .webinar-webinarWindow-statistics-container-text-left, .webinar-webinarWindow-statistics-container-text-right {
        width: 50%;
        height: 100%;
        
        display: flex;
        flex-direction: column;
        justify-content: space-around; 
    }
    
    .webinar-webinarWindow-statistics-container-text-right {
        align-items: flex-end;
    }
    
</style>



