import Webinar from '@/panels/Webinar.vue';
import CreateWebinar from '@/panels/CreateWebinar.vue';
import InfoWebinar from '@/panels/InfoWebinar.vue';
import SettingsWebinar from '@/panels/SettingsWebinar.vue';
 
const routes = [
    {
        path: '/',
        name: "webinar",
        component: Webinar
    },
    {
        path: '/create_webinar',
        name: "create_webinar",
        component: CreateWebinar
    },
    {
        path: '/info_webinar',
        name: "info_webinar",
        component: InfoWebinar
    },
    {
        path: '/settings_webinar',
        name: "settings_webinar",
        component: SettingsWebinar
    },
];
 
export default routes;


