<template>
    <!--контейнер-->
    <div class="create_webinar-header-container-left-options-timezone-input-field">

        <!--Видимый дропдаун-->
        <div class="create_webinar-header-container-left-options-timezone-input-field-left"><p>{{ timezoneGMT }}</p></div>
        <div class="create_webinar-header-container-left-options-timezone-input-field-right">{{ timezoneCity }}
        <img src="@/assets/createWebinar/icon_drop.svg" alt="icon"/></div>

        
        
        <!--Рендер списка часовых поясов-->
        <div class="create_webinar-header-container-left-options-timezone-input-field-drop">
            <div 
                 v-for="timezone in timezones"
                 @click="dropClickTimezone" 
                 class="create_webinar-header-container-left-options-timezone-input-field-drop-item">
                
                <div class="create_webinar-header-container-left-options-timezone-input-field-drop-item-left">{{ timezone.gmt }}</div>
                <div class="create_webinar-header-container-left-options-timezone-input-field-drop-item-right">{{ timezone.city }}</div>
                
            </div>
        </div>
        
    </div>
</template>

<script>
    export default {
        name: 'CreateWebinar_timezone',
        methods: {
            //Выбор часового пояса
            dropClickTimezone(e) {
                this.$store.dispatch('SET_TIMEZONE', {
                    gmt: e.currentTarget.children[0].innerHTML,
                    city: e.currentTarget.children[1].innerHTML});
            },
        },
        computed: {
            //Получение параметров из хранилища
            timezoneGMT() {
                return this.$store.getters.getTimezoneGMT;
            },
            timezoneCity() {
                return this.$store.getters.getTimezoneCity;
            }
        },
        data() {
            return {
                //Список часовых поясов
                timezones: [
                    {gmt: "+3 GMT", city: "Москва"},
                    {gmt: "+7 GMT", city: "Красноярск"},
                ],
            }
        },
    }
</script>

<style>
    .create_webinar-header-container-left-options-timezone-input-field {
        width: 100%;
        height: 100%;
        
        display: flex;
        flex-direction: row;
        align-items: center;
        position: relative;
        
        border-bottom: 1px solid #FFFFFF;
    }
    
    .create_webinar-header-container-left-options-timezone-input-field-left {
        width: 40%;
    }
    
    .create_webinar-header-container-left-options-timezone-input-field-right {
        width: 60%;
        display: flex;
        justify-content: flex-end;
    }
    
    .create_webinar-header-container-left-options-timezone-input-field-right img {
        margin-left: 10px;
    }
    
    .create_webinar-header-container-left-options-timezone-input-field:hover {
        cursor: pointer;
    }
    
    .create_webinar-header-container-left-options-timezone-input-field:hover .create_webinar-header-container-left-options-timezone-input-field-drop {
        visibility: visible;
    }
    
    .create_webinar-header-container-left-options-timezone-input-field-drop {
        visibility: hidden;
        position: absolute;
        top: 25px;
        left: 0;
        z-index: 200;
        
        background: #ffffff;
        padding-top: 21px;
        padding-bottom: 25px;
        padding-left: 7%;
        width: 93%;
        border-radius: 0 0 10px 10px;
        box-shadow: 0px 4px 24px rgba(0, 0, 0, 0.12);
        color: #333333;
    }
    
    .create_webinar-header-container-left-options-timezone-input-field-drop-item {
        display: flex;
        flex-direction: row;
        width: 100%;
        color: #333333;
    }
    
    .create_webinar-header-container-left-options-timezone-input-field-drop-item:hover {
        cursor: pointer;
        color: #2C78F6;
    }

    
    .create_webinar-header-container-left-options-timezone-input-field-drop-item-left {
        width: 40%;
        line-height: 32px;
        font-size: 14px;
        border: none;
        padding: 0; 
    }
    
    .create_webinar-header-container-left-options-timezone-input-field-drop-item-right {
        width: 50%;
        display: flex;
        justify-content: flex-end;
        
        line-height: 32px;
        font-size: 14px;
        border: none;
        padding: 0; 
    }
</style>



