<template>
    <div class="webinar-title">
            <div class="webinar-title-left">
                <p>Вебинары</p>
                <div class="webinar-title_buttonAdd">
                    <p>+ новый вебинар</p>
                </div>
            </div>
            
            <div class="webinar-title-right">
                <div class="webinar-title_buttonOptions">
                    <img src="../../assets/webinar/title-gear.svg" alt="icon" />
                    <p>Настройка канала</p>
                </div>
            </div>
        </div>
</template>

<script>
    export default {
        name: 'Title',
    }
</script>

<style>
    .webinar-title {
        height: 35px;
        margin-top: 2.8vmin;
        
        font-family: 'Montserrat', sans-serif;
        font-size: 24px;
        font-weight: bold;
        color: #30353E;
        
        display: flex;
        flex-direction: row;
        align-items: center;
    }
    
    .webinar-title-left, .webinar-title-right {
        height: 100%;
        width: 50%;
        
        display: flex;
        align-items: center;
        flex-direction: row;
    }
    .webinar-title-right {
        justify-content: flex-end;
    }
    
    .webinar-title_buttonAdd {
        height: 95%;
        width: 148px;
        
        display: flex;
        justify-content: center;
        align-items: center;
        
        font-size: 12px;
        color: #2C78F6;
        
        background-color: #F6F7F9;
        border: solid #2C78F6 2px;
        border-radius: 20px;
        
        margin-left: 14px;
    }
    
    .webinar-title_buttonAdd:hover {
        background-color: #2C78F6;
        color: #F6F7F9;
        cursor: pointer;
    }
    
    .webinar-title_buttonOptions {
        height: 100%;
        width: 174px;
        
        font-size: 12px;
        color: white;
        
        display: flex;
        justify-content: center;
        align-items: center;
        
        border: solid #2C78F6 2px;
        border-radius: 20px;
        background: #2C78F6;
    }
    
    .webinar-title_buttonOptions:hover {
        background-color: #1C63D8;
        cursor: pointer;
    }
    
    .webinar-title_buttonOptions p {
        margin-left: 4px;
    }
</style>


