<template>
    <div id="settings_webinar">
        <Header />
        <Tariff />
        <Integration />
        <Encoder />
        <Buttons />
        
    </div>
</template>

<script>
    import Header from '@/components/settingsWebinar/Header.vue';
    import Tariff from '@/components/settingsWebinar/Tariff.vue';
    import Integration from '@/components/settingsWebinar/Integration.vue';
    import Encoder from '@/components/settingsWebinar/Encoder.vue';
    import Buttons from '@/components/settingsWebinar/Buttons.vue';
    
    export default {
        name: 'SettingsWebinar',
        components: {
            Encoder, Integration, Buttons, Tariff, Header
        }
    }
</script>

<style>
    #settings_webinar {
        width: calc(100%-58px);
        height: auto;
        margin-left: 58px;
        
        display: flex;
        flex-direction: column;
    }

    .settings_webinar-header, .settings_webinar-tariff, .settings_webinar-integration, .settings_webinar-encoder, .settings_webinar-buttons {
        width: 89%;
        font-family: 'Montserrat', sans-serif;
    }
</style>



