                        <div class="settings_webinar-encoder-container-in-left-item-q">
                            <p>RTMP URL мониторинга прямого эфира</p>
                            <Tooltip_question textMessage="URL предназначен для мониторинга прямого эфира, максимум 5 единовременных подключений"/>
                        </div>
                        <p class="settings_webinar-encoder-container-in-left-item-url">rtmp://publish.facecast.net/publisher/nastavnikme?sharedkey=8uksu</p>
                        <div class="settings_webinar-encoder-container-in-left-item-line"></div>
