                    <p><b>РЕКОМЕНДУЕМЫЕ НАСТРОЙКИ</b></p>
                    <p><b>Видео:</b><br/>
                        1280x720, кодек - H.264, Main Profile, Level 3.1, битрейт 2.5 Мбит/с, 30 fps, keyframe - 1 в секунду / каждые 30 кадров</p>
                    <p><b>Аудио:</b><br/>
                        AAC, stereo, 44100 Hz, битрейт 128 кбит/с. Наличие аудиодорожки обязательно!</p>
                </div>
                
            </div>
            
        </div>
    </div>
</template>

<script>
    import Tooltip_question from '@/components/Tooltip_question.vue';
    
    export default {
        name: 'SettingsWebinar_encoder',
        components: {
            Tooltip_question,
        }
    }
