        margin-left: 5.17%;
        width: 89.66%;
        height: 235px;
        
        display: flex;
        flex-direction: row;
    }
    
    .settings_webinar-encoder-container-in-left{
        height: 100%;
        width: 52.7%;
        display: flex;
        flex-direction: column;
        
        
    }
    
    .settings_webinar-encoder-container-in-left-item {
        height: 59px;
        width: 100%;
        margin-top: 28px;
    }
    
