        margin-top: 8px;
        background-color: #B8D2FC;
    }
    
    .settings_webinar-encoder-container-in-right {
        /*height: 213px;*/
        height: 100%;
        width: 38.36%;
        background: #F6F6F6;
        border-radius: 10px;
        padding-left: 3.4%;
        padding-right: 5.44%;
        padding-top: 23px;
    }
    
    .settings_webinar-encoder-container-in-right p {
        line-height: 22px;
        font-size: 12px; 
    }
    
    .settings_webinar-encoder-container-in-right p:first-child {
        margin-bottom: 16px;
    }
    
    .settings_webinar-encoder-container-in-right p:last-child {
        margin-top: 10px;
    }
</style>



