<template>
    <div class="create_webinar-header">
        <div class="create_webinar-header-container">
            <div class="create_webinar-header-container-left">
                <div class="create_webinar-header-container-left-miniTitle"><p class="create_webinar-header-container-left-miniTitle-text">Заголовок трансляции</p></div>
                
                <div class="create_webinar-header-container-left-name">
                    <input type="text" placeholder="Введите заголовок" spellcheck="false"/>
                </div>
                
                <div class="create_webinar-header-container-left-line"></div>
                <div class="create_webinar-header-container-left-options">
                    
                    <div class="create_webinar-header-container-left-options-leftContainer">
                        <div class="create_webinar-header-container-left-options-time">
                            <div class="create_webinar-header-container-left-miniTitle"><p class="create_webinar-header-container-left-miniTitle-text">Время</p></div>
                            
                            <div class="create_webinar-header-container-left-options-time-input">
                                <div class="create_webinar-header-container-left-options-time-input-field">
                                    
                                    <input v-model="timeStart" type="text" placeholder="00:00" spellcheck="false"/>
                                    
                                </div>
                                <div class="create_webinar-header-container-left-options-time-input-center">
                                    <p>-</p>
                                </div>
                                <div class="create_webinar-header-container-left-options-time-input-field">
                                    
                                    <input v-model="timeEnd" type="text" placeholder="23:59" spellcheck="false"/>
                                    
                                </div>
                            </div>
                            
                        </div>

                        <div class="create_webinar-header-container-left-options-date">
                            <div class="create_webinar-header-container-left-miniTitle"><p class="create_webinar-header-container-left-miniTitle-text">Дата</p></div>

                            <div class="create_webinar-header-container-left-options-date-input">
                                <div class="create_webinar-header-container-left-options-date-input-field">
                                    <input type="text" placeholder="дд.мм.гг" spellcheck="false"/>
                                </div>
                                <div class="create_webinar-header-container-left-options-date-input-center">
                                    <p>-</p>
                                </div>
                                <div class="create_webinar-header-container-left-options-date-input-field">
                                    <input type="text" placeholder="дд.мм.гг" spellcheck="false"/>
                                </div>
                            </div>

                        </div>
                    </div>
                    
                    <div class="create_webinar-header-container-left-options-rightContainer">
                        <div class="create_webinar-header-container-left-options-timezone">
                            <div class="create_webinar-header-container-left-miniTitle"><p class="create_webinar-header-container-left-miniTitle-text">Часовой пояс</p></div>
                            <div class="create_webinar-header-container-left-options-timezone-input">

                                <Timezone_dropDown />

                            </div>
                        </div>
                    </div>
                    
                </div>
            </div>
            
            <div class="create_webinar-header-container-right">
                <div class="create_webinar-header-container-right-icon">
                    <img src="../../assets/createWebinar/icon_cover.svg" alt="icon" />
                    <p>ОБЛОЖКА</p>
                </div>
            </div>
            
        </div>
    </div>
</template>

<script>
    import Timezone_dropDown from '@/components/createWebinar/components/Timezone_dropDown.vue';
    
    export default {
        name: 'CreateTranslation',
        components: {
            Timezone_dropDown,
        },
        data() {
            return {
                timeStart: this.$store.getters.getTimeStart,
                timeEnd: this.$store.getters.getTimeEnd,
            };
        },
        watch: {
            timeStart: "timeValidationStart",
            timeEnd: "timeValidationEnd",
        },
        methods: {
            timeValidationStart() {
                const {timeStart} = this;

                if (this.$store.getters.getTimeStart.length == 4 && timeStart.length == 3) 
                    this.$data.timeStart = timeStart.slice(0,2);
                
                if (this.$store.getters.getTimeStart.length == 1 && timeStart.length == 2) 
                    this.$data.timeStart = timeStart + ":";
                else if (timeStart.length > 5) 
                    this.$data.timeStart = timeStart.slice(0,5);
                this.$store.dispatch('SET_TIMESTART', timeStart);
            },
            timeValidationEnd() {
                const {timeEnd} = this;
                
                if (this.$store.getters.getTimeEnd.length == 4 && timeEnd.length == 3) 
                    this.$data.timeEnd = timeEnd.slice(0,2);
                
                if (this.$store.getters.getTimeEnd.length == 1 && timeEnd.length == 2) 
                    this.$data.timeEnd = timeEnd + ":";
                else if (timeEnd.length > 5) 
                    this.$data.timeEnd = timeEnd.slice(0,5);
                this.$store.dispatch('SET_TIMEEND', timeEnd);
            },
        }
    }
</script>

<style>
    .create_webinar-header {
        height: 230px;
        margin-top: 21px;
        border-radius: 10px;
        
        display: flex;
        background: #5da1a6;
    }
    
    .create_webinar-header-container {
        margin-left: 5.17%;
        margin-top: 45px;
        
        height: 128px;
        width: 89.66%;
        
        display: flex;
        flex-direction: row;
    }
    
    .create_webinar-header-container-left-miniTitle-text {
        font-size: 12px;
        color: #FFFFFF;
    }
    
    .create_webinar-header-container-left-miniTitle {
        height: 22px;
        width: auto;
        
        display: flex;
        align-items: center;
    }
    
    .create_webinar-header-container-left {
        width: 57.9%;
        width: 720px;
        height: 100%;
    }
    
    .create_webinar-header-container-left-name {
        margin-top: 3px;
        height: 22px;
        width: 100%;
        
        display: flex;
        align-items: flex-end;
    }
    
    .create_webinar-header-container-left-name input {
        width: 100%;
        border: 0;
        outline: 0;
        background: transparent;
        
        font-size: 14px;
        color: #FFFFFF;
    }
    
    .create_webinar-header-container-left-name input::placeholder {
        color: #B1B1B1;
        font-size: 14px;
    }
    
    .create_webinar-header-container-left-line {
        height: 1px;
        width: 100%;
        margin-top: 4px;
        margin-bottom: 25px;
        background: #FFFFFF;
    }
    
    .create_webinar-header-container-left-options {
        width: 100%;
        height: 51px;
        
        display: flex;
        flex-direction: row;
    }
    
    .create_webinar-header-container-left-options-leftContainer,
    .create_webinar-header-container-left-options-rightContainer {
        display: flex;
        flex-direction: row;
    }
    
    .create_webinar-header-container-left-options-leftContainer {
        width: 65%;
    }
    
    .create_webinar-header-container-left-options-rightContainer {
        width: 35%;
    }
    
    .create_webinar-header-container-left-options-time {
        width: 32.4%;
        width: 206px;
        height: 100%;
        
        display: flex;
        flex-direction: column;
    }
    
    .create_webinar-header-container-left-options-time-input-field, .create_webinar-header-container-left-options-date-input-field {
        width: 70px;
        height: 100%;
        
        display: flex;
        align-items: center;
        
        border-bottom: 1px solid #FFFFFF;
    }
    
    .create_webinar-header-container-left-options-time-input-field input, .create_webinar-header-container-left-options-date-input-field input {
        width: 100%;
        height: 100%;
        border: 0;
        outline: 0;
        background: transparent;
        
        font-size: 14px;
        color: #FFFFFF;
    }
    
    .create_webinar-header-container-left-options-time-input-field input::placeholder, .create_webinar-header-container-left-options-date-input-field input::placeholder {
        color: #B1B1B1;
        font-size: 14px;
    }
    
    .create_webinar-header-container-left-options-time-input-center, .create_webinar-header-container-left-options-date-input-center {
        width: 10.2%;
        height: 100%;
        
        margin-left: 2.1%;
        margin-right: 2.1%;
        
        display: flex;
        justify-content: center;
        align-items: center;
        
        color: #FFFFFF;
        font-size: 14px;
        font-weight: 600;
    }
    
    .create_webinar-header-container-left-options-time-input, .create_webinar-header-container-left-options-date-input, .create_webinar-header-container-left-options-timezone-input {
        margin-top: 4px;
        height: 25px;
        width: 100%;
        
        font-size: 14px;
        color: #B1B1B1;
        
        display: flex;
        flex-direction: row;
    }
    
    .create_webinar-header-container-left-options-timezone-input {
        color: #FFFFFF;
        font-size: 14px;
    }
    
    .create_webinar-header-container-left-options-date {
        width: 33.6%;
        height: 100%;
        
        display: flex;
        flex-direction: column;
    }
    
    .create_webinar-header-container-left-options-timezone {
        width: 100%;
        height: 100%;
        
        display: flex;
        flex-direction: column;
    }
    
    .create_webinar-header-container-right {
        width: 42.1%;
        height: 100%;
        
        display: flex;
        justify-content: flex-end;
        align-items: center;
    }
    
    .create_webinar-header-container-right-icon {
        height: 93px;
        width: 93px;
        border-radius: 100px;
        
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        
        background-color: #FFFFFF;
    }
    
    .create_webinar-header-container-right-icon:hover {
        cursor: pointer;
    }
    
    .create_webinar-header-container-right-icon p {
        margin-top: 8.05px;
        font-size: 10px;
        color: #6C6C6C;
    }
</style>



