<template>
    <div class="settings_webinar-buttons">
        <div @click="gotoInfo_Webinar()" class="settings_webinar-buttons-save">
            <p>СОХРАНИТЬ НАСТРОЙКИ</p>
        </div>
        <div class="settings_webinar-buttons-cancel">
            <p>ОТМЕНИТЬ ИЗМЕНЕНИЯ</p>
        </div>
    </div>
</template>

<script>
    export default {
        name: 'SettingsWebinar_buttons',
        methods: {
            gotoInfo_Webinar() {
                this.$router.push({name: "info_webinar"});
            },
        }
    }
</script>

<style>
    .settings_webinar-buttons {
        height: 50px;
        margin-top: 32px;
        margin-bottom: 216px;
        
        display: flex;
        flex-direction: row;
    }
    
    .settings_webinar-buttons-save {
        height: 45px;
        width: 230px;
        border-radius: 5px;
        
        display: flex;
        justify-content: center;
        align-items: center;
        
        color: #FFFFFF;
        font-size: 12px;
        font-weight: bold;
        
        margin-right: 20px;
        
        background-color: #2C78F6;
    }
    
    .settings_webinar-buttons-save:hover {
        cursor: pointer;
        background-color: #1C63D8;
    }
    
    .settings_webinar-buttons-cancel {
        height: 45px;
        width: 230px;
        border-radius: 5px;
        
        display: flex;
        justify-content: center;
        align-items: center;
        
        color: #2C78F6;
        font-size: 12px;
        font-weight: bold;
        
        background-color: #F6F7F9;
        border: 1px #2C78F6 solid;
    }
    
    .settings_webinar-buttons-cancel:hover {
        color: #FFFFFF;
        background-color: #2C78F6;
        cursor: pointer;
    }
</style>



