<template>
    <div id="create_webinar">
        <BackPanel />
        <Title />
        <Header />
        <OptionInfo />
    </div>
</template>

<script>
    import BackPanel from '../components/createWebinar/BackPanel.vue';
    import Title from '../components/createWebinar/Title.vue';
    import Header from '../components/createWebinar/Header.vue';
    import OptionInfo from '../components/createWebinar/OptionInfo.vue';

    export default {
        name: 'CreateWebinar',
        components: {
            Title, BackPanel, OptionInfo, Header
        }
    }
</script>

<style>
    #create_webinar {
        width: calc(100%-58px);
        height: auto;
        margin-left: 58px;
    }

    .create_webinar-backPanel, .create_webinar-title, .create_webinar-header, .create_webinar-optionInfo {
        width: 89%;
        font-family: 'Montserrat', sans-serif;
    }
</style>



