<template>
    <div class="info_webinar-header">
        <div class="info_webinar-header-container">
            <div class="info_webinar-header-container-left">
                
                
                <div class="info_webinar-header-container-left-tags">
                    <div class="info_webinar-header-container-left-tags-item">
                        <div class="circle_icon"></div>
                        <p class="theme">Пародонтология</p>
                    </div>
                    <div class="info_webinar-header-container-left-tags-item">
                        <p class="type">Вебинар</p>
                    </div>
                </div>
                
                
                <div class="info_webinar-header-container-left-name">
                    <p>Минимально инвазивные методики лечения фронтальной группы зубов</p>
                </div>
                
                
                <div class="info_webinar-header-container-left-info">
                    <div class="info_webinar-header-container-left-info-item">
                        <img src="@/assets/infoWebinar/icon_calendar.svg" alt="icon" />
                        <p>16.01.2020</p>
                    </div>
                    <div class="info_webinar-header-container-left-info-item">
                        <img src="@/assets/infoWebinar/icon_clock.svg" alt="icon" />
                        <p>16:00 – 18:00</p>
                    </div>
                    <div class="info_webinar-header-container-left-info-item">
                        <p>+3 GMT (Москва)</p>
                    </div>
                </div>
                
                
            </div>
            <div class="info_webinar-header-container-right">
                <div class="info_webinar-header-container-right-box">
                    <img src="@/assets/infoWebinar/icon_center.svg" alt="icon" />
                    <p>Питер Экспо<br/><span>ОРГАНИЗАТОР КУРСА</span></p>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: 'CreateWebinar_header',
    }
</script>

<style>  
    .info_webinar-header {
        margin-top: 35px;
        height: 230px;
        background: #5da1a6;
        border-radius: 10px;
        
        display: flex;
    }
    
    .info_webinar-header-container {
        margin-top: 39px;
        height: 151px;
        
        display: flex;
        flex-direction: row;
    }
    
    .info_webinar-header-container-left {
        height: 100%;
        width: 65%;
    }
    
    .info_webinar-header-container-left-tags {
        width: 100%;
        height: 26px;
        
        display: flex;
        flex-direction: row;
    }
    
    .info_webinar-header-container-left-tags div {
        margin-left: 11px;
    }
    
    .info_webinar-header-container-left-tags div:first-child {
        margin-left: 0px;
    }
    
    .info_webinar-header-container-left-tags-item {
        height: 100%;
        padding-left: 10px;
        padding-right: 10px;
        flex-basis: content;
        
        display: flex;
        flex-direction: row;
        justify-content: center;
        align-items: center;
        
        border-radius: 33px;
        background: #FFFFFF;
    }
    
    .info_webinar-header-container-left-tags-item .circle_icon {
        width: 8px;
        height: 8px;
        background-color: #2BC3A8;
        margin-right: 4px;
        border-radius: 73px;
    }
    
    .info_webinar-header-container-left-tags-item .theme {
        font-size: 12px;
        font-weight: 600;
        color: #30353E;
    }
    
    .info_webinar-header-container-left-tags-item .type {
        font-size: 12px;
        font-weight: 600;
        color: #2C78F6;
    }
    
    .info_webinar-header-container-left-name {
        width: 100%;
        margin-top: 22px;
        height: 52px;
    }
    
    .info_webinar-header-container-left-name p {
        font-size: 24px;
        color: #FFFFFF;
    }
    
    .info_webinar-header-container-left-info {
        width: 100%;
        margin-top: 29px;
        height: 22px;
        
        display: flex;
        flex-direction: row;
    }
    
    .info_webinar-header-container-left-info-item {
        margin-left: 4.18%;
        height: 100%;
        
        display: flex;
        flex-direction: row;
        align-items: center;
    }
    
    .info_webinar-header-container-left-info .info_webinar-header-container-left-info-item:first-child {
        width: 102px;
        margin-left: 0px;
    }
    
    .info_webinar-header-container-left-info .info_webinar-header-container-left-info-item:nth-child(2) {
        width: 115px;
    }
    
    .info_webinar-header-container-left-info .info_webinar-header-container-left-info-item:last-child {
        max-width: 216px;
    }
    
    .info_webinar-header-container-left-info-item p {
        font-size: 14px;
        color: #FFFFFF;
    }
    
    .info_webinar-header-container-left-info-item img {
        margin-right: 12.06%;
    }
    
    .info_webinar-header-container-right {
        height: 100%;
        width: 35%;
        
        display: flex;
        justify-content: flex-end;
    }
    
    .info_webinar-header-container-right-box {
        height: 37px;
        
        display: flex;
        align-items: center;
        flex-direction: row;
    }
    
    .info_webinar-header-container-right-box p {
        margin-left: 11px;
        
        color: #FFFFFF;
        font-size: 16px;
        font-weight: 600;
    }
    
    .info_webinar-header-container-right-box p span {
        font-size: 10px;
        font-weight: lighter;
    }
</style>



