<template>
    <div class="create_webinar-backPanel">
        <img @click="gotoWebinar()" src="../../assets/createWebinar/icon_back.svg" alt="back" />
        <p @click="gotoWebinar()">Назад</p>
    </div>
</template>

<script>
    export default {
        name: 'BackPanel',
        methods: {
            gotoWebinar() {
                this.$router.push({name: "webinar"});
            }
        }
    }
</script>

<style>
    .create_webinar-backPanel {
        height: 38px;
        margin-top: 18px;

        display: flex;
        flex-direction: row;
        align-items: center;
        color: #2C78F6;
        font-size: 12px;
        font-weight: bold;
    }
    
    .create_webinar-backPanel:hover {
        cursor: pointer;
    }

    .create_webinar-backPanel p {
        margin-left: 8.5px;
    }
    
    .create_webinar-backPanel p:hover {
        cursor: pointer;
    }
</style>



