<template>
    <div class="create_webinar-optionInfo-bottom">
        <div @click="gotoInfo_Webinar()" class="create_webinar-optionInfo-bottom-button">
            <p>Сохранить</p>
        </div>
    </div>
</template>

<script>
    export default {
        name: 'OptionInfo_bottom',
        methods: {
            gotoInfo_Webinar() {
                this.$router.push({name: "info_webinar"});
            }
        }
    }
</script>

<style>
    .create_webinar-optionInfo-bottom {
        height: 139px;
        width: 89.66%;
        margin-left: 5.17%;

        display: flex;
        flex-direction: row;
    }

    .create_webinar-optionInfo-bottom-button {
        height: 45px;
        width: 154px;
        margin-top: 29px;
        background: #2C78F6;
        border-radius: 5px;

        display: flex;
        justify-content: center;
        align-items: center;

        color: #FFFFFF;
        font-size: 12px;
        font-weight: bold;
    }
    
    .create_webinar-optionInfo-bottom-button:hover {
        background-color: #1C63D8;
        cursor: pointer;
    }
</style>



