<template>
    <div class="create_webinar-optionInfo-center-container-center-time">
        <div class="create_webinar-optionInfo-center-container-center-time-left"><p>{{ AcessTime }}</p></div>
        <div class="create_webinar-optionInfo-center-container-center-time-right"><img src="@/assets/settingsWebinar/icon_drop.svg" alt="icon" /></div>

        <div class="create_webinar-optionInfo-center-container-center-time-dropDown">
            <p @click="onChangeAcessTime">01:00ч</p>
            <p @click="onChangeAcessTime">02:00ч</p>
            <p @click="onChangeAcessTime">03:00ч</p>
            <p @click="onChangeAcessTime">04:00ч</p>
            <p @click="onChangeAcessTime">05:00ч</p>
            <p @click="onChangeAcessTime">06:00ч</p>
            <p @click="onChangeAcessTime">07:00ч</p>
            <p @click="onChangeAcessTime">08:00ч</p>
        </div>
    </div>
</template>

<script>
    export default {
        name: 'OptionInfo_center_timeAcess',
        methods: {
            //Выбор времени отключения
            onChangeAcessTime(e) {
                this.$store.dispatch('SET_ACESSTIME', e.currentTarget.innerHTML);
            },
        },
        computed: {
            //Получение параметров из хранилища
            AcessTime() {
                return this.$store.getters.getAcessTime;
            },
        },
    }
</script>

<style>
    .create_webinar-optionInfo-center-container-center-time {
        display: flex;
        flex-direction: row;
        width: 103px;
        margin-right: 0;
        margin-left: 19px;
        position: relative;
        
        border-bottom: 1px solid #C4C4C4;
    }
    
    .create_webinar-optionInfo-center-container-center-time:hover .create_webinar-optionInfo-center-container-center-time-dropDown {
        visibility: visible;
    }
    
    .create_webinar-optionInfo-center-container-center-time-left {
        display: flex;
        width: 80%;
        margin-right: 0;
    }
    
    .create_webinar-optionInfo-center-container-center-time-left,
    .create_webinar-optionInfo-center-container-center-time-right {
        cursor: pointer;
    }
    
    .create_webinar-optionInfo-center-container-center-time-right {
        display: flex;
        width: 20%;
        margin-right: 0;
    }
    
    .create_webinar-optionInfo-center-container-center-time-dropDown {
        visibility: hidden;
        position: absolute;
        top: 23px;
        left: 0;
        
        background: #ffffff;
        padding-top: 21px;
        padding-bottom: 25px;
        padding-left: 7%;
        width: 93%;
        border-radius: 0 0 10px 10px;
        box-shadow: 0px 4px 24px rgba(0, 0, 0, 0.12);
    }
    
    .create_webinar-optionInfo-center-container-center-time-dropDown p {
        line-height: 32px;
        font-size: 14px;
        color: #333333;
        border: none;
        padding: 0; 
    }
    
    .create_webinar-optionInfo-center-container-center-time-dropDown p:hover {
        cursor: pointer;
        color: #2C78F6;
    }
</style>



