const store = {
    state: {
        timezone: {
            gmt: "+3 GMT",
            city: "(Москва)",
        },
        acessType: "Свободный доступ",
        acessTime: "12:00ч",
        
        timeStart: "",
        timeEnd: "",
    },
    actions: {
        SET_TIMEZONE({commit}, newTimezone) {commit("setTimezone", newTimezone);},
        
        SET_ACESSTYPE({commit}, newAcessType) {commit("setAcessType", newAcessType);},
        SET_ACESSTIME({commit}, newAcessTime) {commit("setAcessTime", newAcessTime);},
        
        SET_TIMESTART({commit}, newTimeStart) {commit("SetTimeStart", newTimeStart);},
        SET_TIMEEND({commit}, newTimeEnd) {commit("SetTimeEnd", newTimeEnd);},
    },
    mutations: {
        setTimezone (state, newTimezone) {
            state.timezone.gmt = newTimezone.gmt;
            state.timezone.city = newTimezone.city;
        },
        setAcessType (state, newAcessType) {state.acessType = newAcessType;},
        setAcessTime (state, newAcessTime) {state.acessTime = newAcessTime;},
        
        SetTimeStart (state, newTimeStart) {state.timeStart = newTimeStart;},
        SetTimeEnd (state, newTimeEnd) {state.timeEnd = newTimeEnd;},
    },
    getters: {
        getTimezoneGMT(state) {return state.timezone.gmt;},
        getTimezoneCity(state) {return state.timezone.city;},
        
        getAcessType(state) {return state.acessType;},
        getAcessTime(state) {return state.acessTime;},
        
        getTimeStart(state) {return state.timeStart;},
        getTimeEnd(state) {return state.timeEnd;},
    }
};
 
export default store;


