<template>
    <div class="plannedWebinars-table-body-element">
        <div class="plannedWebinars-table-body-element-video">
            <div class="plannedWebinars-table-body-element-video-icon">
                <img src="../../../assets/webinar/play-button.svg" alt="icon" />
            </div>
            <div class="plannedWebinars-table-body-video-content">

            </div>
        </div>

        <div class="plannedWebinars-table-body-element-date">
            <p>12.10.19-13.10.19</p>
            <p>4 ч. 55 мин.  до старта</p>
        </div>

        <div class="plannedWebinars-table-body-element-name">
            <p>Семинар «Подробный ортопедический протокол»</p>
            <p>1 занятие</p>
        </div>

        <div class="plannedWebinars-table-body-element-price">
            <p>-</p>
        </div>
        <div class="plannedWebinars-table-body-element-rate">
            <p>-</p>
        </div>
    </div>
</template>

<script>  
    export default {
        name: 'PlannedWebinarsElement',
    }
</script>

<style>
    .plannedWebinars-table-body-element {
        width: 100%;
        height: 70px;
        font-family: 'Montserrat', sans-serif;
        margin-top: 10px;
        
        display: flex;
        flex-direction: row;
    }
    
    .plannedWebinars-table-body-element-video {
        width: 11.35%;
        height: 100%;
        margin-right: 1.2%;
        position: relative;
        
        display: flex;
        justify-content: center;
        align-items: center;
    }
    
    .plannedWebinars-table-body-element-video:hover {
        cursor: pointer;
    }
    
    .plannedWebinars-table-body-element-video-icon {
        width: 35px;
        height: 35px;
        position: absolute;
    }
    
    .plannedWebinars-table-body-video-content {
        width: 100%;
        height: 100%;
        border-radius: 5px;
        background: grey;
    }
    
    .plannedWebinars-table-body-element-date {
        width: 12.90%;
        padding-left: 1.2%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        background-color: white;
        border-radius: 5px 0 0 5px;
    }
    
    .plannedWebinars-table-body-element-date p:first-child {
        font-size: 12px;
        color: #333333;
    }
    
    .plannedWebinars-table-body-element-date p:last-child {
        margin-top: 5px;
        color: #999999;
        font-size: 12px;
    }
    
    .plannedWebinars-table-body-element-name {
        width: 35.79%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        background-color: white;
    }
    
    .plannedWebinars-table-body-element-name p:first-child {
        font-size: 12px;
        color: #333333;
    }
    
    .plannedWebinars-table-body-element-name p:last-child {
        margin-top: 5px;
        color: #999999;
        font-size: 12px;
    }
    
    .plannedWebinars-table-body-element-price {
        width: 21.62%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        background-color: white;
    }
    
    .plannedWebinars-table-body-element-price p:first-child {
        font-size: 12px;
        color: #333333;
    }
    
    .plannedWebinars-table-body-element-rate {
        width: 15.94%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        background-color: white;
        border-radius: 0 5px 5px 0;
    }
    
    .plannedWebinars-table-body-element-rate p:first-child {
        font-size: 12px;
        color: #333333;
    }
</style>



