<template>
    <!--Видимый дропдаун-->
    <div class="settings_webinar-header-container-in-urlBlock-downBlock-timezone-field">
        <div class="settings_webinar-header-container-in-urlBlock-downBlock-timezone-field-left">{{timezoneGMT}} {{timezoneCity}}</div>
        <div class="settings_webinar-header-container-in-urlBlock-downBlock-timezone-field-right"><img src="@/assets/settingsWebinar/icon_drop.svg" alt="icon"/></div>

        <!--Рендер списка часовых поясов-->
        <div class="settings_webinar-header-container-in-urlBlock-downBlock-timezone-field-drop">
            <div 
                 v-for="timezone in timezones"
                 @click="dropClickTimezone" 
                 class="settings_webinar-header-container-in-urlBlock-downBlock-timezone-field-drop-item">

                <p>{{ timezone.gmt }}</p><p>&nbsp;</p> <p> ({{ timezone.city }}) </p></div>

        </div>
    </div>
</template>

<script>
    export default {
        name: 'SettingsWebinar_timezone',
        methods: {
            //Выбор часового пояса
            dropClickTimezone(e) {
                this.$store.dispatch('SET_TIMEZONE', {
                    gmt: e.currentTarget.children[0].innerHTML,
                    city: e.currentTarget.children[2].innerHTML});
            },
        },
        computed: {
            //Получение параметров из хранилища
            timezoneGMT() {
                return this.$store.getters.getTimezoneGMT;
            },
            timezoneCity() {
                return this.$store.getters.getTimezoneCity;
            }
        },
        data() {
            return {
                //Список часовых поясов
                timezones: [
                    {gmt: "+3 GMT", city: "Москва"},
                    {gmt: "+7 GMT", city: "Красноярск"},
                ],
            }
        },
    }
</script>

<style>
    .settings_webinar-header-container-in-urlBlock-downBlock-timezone-field {
        width: 100%;
        height: 22px;
        padding-bottom: 4px;
        border-bottom: 1px solid #B8D2FC;
        
        display: flex;
        flex-direction: row;
        
        position: relative;
    }
    
    .settings_webinar-header-container-in-urlBlock-downBlock-timezone-field:hover .settings_webinar-header-container-in-urlBlock-downBlock-timezone-field-drop {
        visibility: visible;
    }
    
    .settings_webinar-header-container-in-urlBlock-downBlock-timezone-field-drop {
        visibility: hidden;
        position: absolute;
        top: 26px;
        left: 0;
        
        background: #ffffff;
        padding-top: 21px;
        padding-bottom: 25px;
        padding-left: 7%;
        width: 93%;
        border-radius: 0 0 10px 10px;
        box-shadow: 0px 4px 24px rgba(0, 0, 0, 0.12);
    }
    
    .settings_webinar-header-container-in-urlBlock-downBlock-timezone-field-drop-item {
        width: 100%;
        display: flex;
        flex-direction: row;
        
        line-height: 32px;
        font-size: 14px;
        color: #333333;
        border: none;
        padding: 0; 
    }
    
    .settings_webinar-header-container-in-urlBlock-downBlock-timezone-field-drop-item:hover {
        cursor: pointer;
        color: #2C78F6;
    }
    
    .settings_webinar-header-container-in-urlBlock-downBlock-timezone-field-left {
        width: 70%;
        display: flex;
        flex-direction: row;
        align-items: center;
    }
    
    .settings_webinar-header-container-in-urlBlock-downBlock-timezone-field-right {
        width: 30%;
        display: flex;
        justify-content: flex-end;
    }
</style>



