<template>
    <div class="create_webinar-optionInfo">
        <Top />
        <div class="create_webinar-optionInfo-line1"></div>
        <Center />
        <div class="create_webinar-optionInfo-line2"></div>
        <Bottom />
    </div>
</template>

<script>
    import Top from './OptionInfo_top.vue';
    import Center from './OptionInfo_center.vue';
    import Bottom from './OptionInfo_bottom.vue';

    export default {
        name: 'OptionInfo',
        components: {
            Top, Center, Bottom,
        }
    }
</script>

<style>
    .create_webinar-optionInfo {
        height: 748px;
        margin-top: 11px;
        margin-bottom: 144px;
        background: #FFFFFF;
        border-radius: 10px;

        display: flex;
        flex-direction: column;
    }

    .create_webinar-optionInfo-line1 {
        height: 1px;
        width: 89.66%;
        margin-left: 5.17%;

        background: #F0F0F0;
    }

    .create_webinar-optionInfo-line2 {
        height: 1px;
        width: 100%;
        background: #F0F0F0;
    }
</style>



