<template>
    <div class="settings_webinar-header">

        <p class="settings_webinar-header-title">Настройка канала</p>

        <div class="settings_webinar-header-container">
            <div class="settings_webinar-header-container-in">
                
                <div class="settings_webinar-header-container-in-title">
                    <p>Server URL</p>
                    <Tooltip_question textMessage="URL предназначен для мониторинга прямого эфира, максимум 5 единовременных подключений"/>
                </div>
                
                <div class="settings_webinar-header-container-in-urlBlock">
                    <p>https://stomup.ru/</p>
                    <div class="settings_webinar-header-container-in-urlBlock-input">
                        <input type="input" placeholder="piterexpo" spellcheck="false"/>
                    </div>
                    <p>/channel</p>
                </div>
                
                <div class="settings_webinar-header-container-in-urlBlock-downBlock">
                    <div class="settings_webinar-header-container-in-urlBlock-downBlock-name">
                        <div class="settings_webinar-header-container-in-title">
                            <p>Название канала</p>
                            <Tooltip_question textMessage="URL предназначен для мониторинга прямого эфира, максимум 5 единовременных подключений"/>
                        </div>
                        
                        <input type="input" placeholder="piterexpo" spellcheck="false"/>
                    </div>
                    
                    <div class="settings_webinar-header-container-in-urlBlock-downBlock-timezone">
                        <div class="settings_webinar-header-container-in-title">
                            <p>Часовой пояс</p>
                        </div>
                        
                        <Timezone_dropDown />
                        
                    </div>
                </div>
                
            </div>
        </div>
    </div>
</template>

<script>
    import Timezone_dropDown from '@/components/settingsWebinar/components/Timezone_dropDown.vue';
    import Tooltip_question from '@/components/Tooltip_question.vue';
    
    export default {
        name: 'SettingsWebinar',
        components: {
            Timezone_dropDown, Tooltip_question,
        },
    }
</script>

<style>
    .settings_webinar-header {
        margin-top: 34px;
        height: 265px;
        display: flex;
        flex-direction: column;
    }
    
    .settings_webinar-header-title {
        height: 38px;
        width: 100%;
        
        font-size: 28px;
        font-weight: bold;
        color: #30353E;
        
        
        margin-bottom: 22px;
        
        display: flex;
        align-items: center;
    }
    
    .settings_webinar-header-container {
        width: 100%;
        height: 273px;
        display: flex;
        
        border-radius: 10px;
        background-color: #FFFFFF;
    }
    
    .settings_webinar-header-container-in {
        margin-top: 28px;
        height: 138px;
        margin-left: 5.17%;
        width: 89.66%;
    }
    
    .settings_webinar-header-container-in-title {
        display: flex;
        align-items: center;
        flex-direction: row;
        height: 22px;
        width: 100%;
        margin-bottom: 5px;
    }
    
    .settings_webinar-header-container-in-title p {
        margin-right: 13px;
        color: #30353E;
        font-size: 12px;
        font-weight: bold;
    }
    
    .settings_webinar-header-container-in-urlBlock {
        height: 27px;
        width: 100%;
        display: flex;
        flex-direction: row;
    }
    
    .settings_webinar-header-container-in-urlBlock p {
        color: #747474;
        height: 22px;
        display: flex;
        align-items: center;
        font-size: 16px;
        font-weight: lighter;
    }
    
    .settings_webinar-header-container-in-urlBlock p:first-child {
        width: 145px;
    }
    
    .settings_webinar-header-container-in-urlBlock p:last-child {
        width: 73px;
        display: flex;
        justify-content: flex-end;
    }
    
    .settings_webinar-header-container-in-urlBlock-input {
        height: 100%;
        width: 82.8%;
        padding-left: 0.5%;
        padding-right: 0.5%;
        display: flex;
        flex-direction: column;
    }
    
    .settings_webinar-header-container-in-urlBlock-input input,
    .settings_webinar-header-container-in-urlBlock-downBlock-name input {
        width: 100%;
        height: 22px;
        border: 0;
        outline: 0;
        background: transparent;
        padding-bottom: 4px;
        border-bottom: 1px solid #B8D2FC;
        
        font-size: 14px;
        color: #333333;
        font-weight: normal;
    }
    
    .settings_webinar-header-container-in-urlBlock-input input::placeholder,
    .settings_webinar-header-container-in-urlBlock-downBlock-name input::placeholder {
        font-size: 16px;
        color: #333333;
        font-weight: normal;
    }
    
    .settings_webinar-header-container-in-urlBlock-downBlock {
        margin-top: 30px;
        height: 53px;
        width: 100%;
        
        display: flex;
        flex-direction: row;
    }
    
    .settings_webinar-header-container-in-urlBlock-downBlock-name {
        height: 100%;
        width: 60%;
        display: flex;
        flex-direction: column;
    }
    
    .settings_webinar-header-container-in-urlBlock-downBlock-timezone {
        height: 100%;
        width: 33.7%;
        display: flex;
        flex-direction: column;
        margin-left: 6.3%;
    }
    
    
    
    .tooltip_question {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 20px;
        height: 20px;
        position: relative;
    }
    
    .tooltip_question_container {
        position: absolute;
        top: -25spx;
        left: 28px;
        
        padding-left: 16px;
        padding-right: 12px;
        padding-top: 12px;
        padding-bottom: 14px;
        width: 283px;
        height: auto;
        
        background: #ffffff;
        border-radius: 10px;
        box-shadow: 1px 1px 10px rgba(129, 129, 129, 0.25);
        z-index: 2000;
    }
    
    .tooltip_question_container p {
        color: #333333;
        font-size: 10px;
        line-height: 12px;
        font-weight: lighter;
    }
</style>



