<template>
        <div class="nullWebinars-table-body-element">
            <p v-if="panel == 'past'">Нет прошедших вебинаров</p>
            <p v-else-if="panel == 'planned'">Трансляций ещё не запланировано</p>
        </div>
</template>

<script>
    export default {
        name: 'WebinarsIsNull',
        props: {
            panel: String,
        }
    }
</script>

<style>
    .nullWebinars-table-body-element{
        width: 100%;
        height: 70px;
        
        font-family: 'Montserrat', sans-serif;
        font-size: 14px;
        color: #7A7A7A;
        font-weight: normal;
        
        margin-top: 10px;
        background: #FFFFFF;
        border-radius: 5px;
        
        display: flex;
        justify-content: center;
        align-items: center;
    }
</style>



