<template>
    <div id="webinar">
        <Title />
        <WebinarWindow />
        <PlannedWebinars />
        <PastWebinars />
    </div>
</template>

<script>
    import Title from '../components/webinar/Title.vue';
    import WebinarWindow from '../components/webinar/WebinarWindow.vue';
    import PlannedWebinars from '../components/webinar/PlannedWebinars.vue';
    import PastWebinars from '../components/webinar/PastWebinars.vue';
    
    export default {
        name: 'Webinar',
        components: {
            Title,
            WebinarWindow,
            PlannedWebinars,
            PastWebinars,
        },
    }
</script>

<style>
    #webinar {
        width: calc(100%-58px);
        height: auto;
        margin-left: 58px;
    }
    
    .webinar-pastWebinars, .webinar-plannedWebinars, .webinar-title, .webinar-webinarWindow {
        width: 89%;
        font-family: 'Montserrat', sans-serif;
    }
</style>



