<template>
    <div id="info_webinar">
        <Header />
        <Content />
    </div>
</template>

<script>
    import Header from '../components/infoWebinar/Header.vue';
    import Content from '../components/infoWebinar/Content.vue';
    
    export default {
        name: 'CreateWebinar',
        components: {
            Header, Content
        }
    }
</script>

<style>
    #info_webinar {
        width: calc(100%-58px);
        height: auto;
        margin-left: 58px;
    }

    .info_webinar-content, .info_webinar-header {
        width: 89%;
        font-family: 'Montserrat', sans-serif;
    }
    
    .info_webinar-header-container, .info_webinar-content-container {
        margin-left: 5.17%;
        width: 89.66%;
    }
</style>



