<template>
    <!--контейнер-->
    <div class="create_webinar-optionInfo-top-right-container-acess">

        <!--поле-->
        <div class="create_webinar-optionInfo-top-right-container-acess-left"><p>{{ acessType }}</p></div>
        <div class="create_webinar-optionInfo-top-right-container-acess-right"><img src="@/assets/settingsWebinar/icon_drop.svg" alt="icon" /></div>

        <!--выпадающий список-->
        <div class="create_webinar-optionInfo-top-right-container-acess-dropDown">
            <p @click="onChangeAcessType">Свободный доступ</p>
            <p @click="onChangeAcessType">Приватная комната</p>
            <p @click="onChangeAcessType">Доступ по приглашению</p>
        </div>

    </div>
</template>

<script>
    export default {
        name: 'OptionInfo_top_acess',
        methods: {
            //Выбор типа доступа
            onChangeAcessType (e) {
                this.$store.dispatch('SET_ACESSTYPE', e.currentTarget.innerHTML);
            },
        },
        computed: {
            //Получение параметров из хранилища
            acessType () {
                return this.$store.getters.getAcessType;
            },
        },
    }
</script>

<style>
    .create_webinar-optionInfo-top-right-container-acess {
        height: 25px;
        width: 100%;

        position: relative;
        display: flex;
        flex-direction: row;
        align-items: center;
        border-bottom: 1px solid #61656B;
    }
    
    .create_webinar-optionInfo-top-right-container-acess-left {
        width: 70%;
    }
    
    .create_webinar-optionInfo-top-right-container-acess-right {
        width: 30%;
        display: flex;
        justify-content: flex-end;
    }
    
    .create_webinar-optionInfo-top-right-container-acess p {
        line-height: 22px;
        font-size: 14px;
        color: #333333;
        width: 100%;
        padding-bottom: 4px;
    }
    
    .create_webinar-optionInfo-top-right-container-acess:hover .create_webinar-optionInfo-top-right-container-acess-dropDown{
        visibility: visible;
    }
    
    .create_webinar-optionInfo-top-right-container-acess p:hover {
        cursor: pointer;
    }
    
    .create_webinar-optionInfo-top-right-container-acess-dropDown {
        visibility: hidden;
        position: absolute;
        top: 26px;
        left: 0;
        z-index: 200;
        
        background: #ffffff;
        padding-top: 21px;
        padding-bottom: 25px;
        padding-left: 7%;
        width: 93%;
        border-radius: 0 0 10px 10px;
        box-shadow: 0px 4px 24px rgba(0, 0, 0, 0.12);
    }
    
    .create_webinar-optionInfo-top-right-container-acess-dropDown p {
        line-height: 32px;
        font-size: 14px;
        color: #333333;
        border: none;
        padding: 0; 
    }
    
    .create_webinar-optionInfo-top-right-container-acess-dropDown p:hover {
        cursor: pointer;
        color: #2C78F6;
    }
    
    .create_webinar-optionInfo-top-right-container-acess input {
        width: 100%;
        border: 0;
        outline: 0;
        background: transparent; 
        font-size: 14px;
        border-bottom: 1px solid rgba(51, 51, 51, 0.3);
        padding-bottom: 2px;
    }
</style>



