<template>
    <div class="webinar-pastWebinars">
        <div class="webinar-pastWebinars-title">
            <p>Прошедшие вебинары</p>
        </div>

        <div class="webinar-pastWebinars-table">
            <div class="webinar-pastWebinars-table-header">
                <p></p>
                <p>ДАТА</p>
                <p>НАЗВАНИЕ / ОПИСАНИЕ</p>
                <p>БИЛЕТЫ</p>
                <p>МАКСИМУМ ЗРИТЕЛЕЙ</p>
                <p>ВСЕГО ПРОСМОТРОВ</p>
                <p>СРОК ХРАНЕНИЯ</p>
            </div>

            <div v-if="isPastWebinars" class="webinar-pastWebinars-table-body">
                <PastWebinarsElement />
                <PastWebinarsElement />
                <PastWebinarsElement />
            </div>
            
            <div v-else class="webinar-pastWebinars-table-body">
                <WebinarsIsNull panel="past"/>
            </div>
        </div> 

    </div>
</template>

<script>
    import PastWebinarsElement from './components/PastWebinarsElement.vue';
    import WebinarsIsNull from './components/WebinarsIsNull.vue';
    
    export default {
        name: 'PastWebinars',
        components: {
            PastWebinarsElement,
            WebinarsIsNull,
        },
        data() {
            return {
                isPastWebinars: false,
            }
        },
    }
</script>

<style>
    .webinar-pastWebinars {
        margin-top: 29px;
        height: auto;
        
        display: flex;
        flex-direction: column;
        font-weight: 600;
        
        margin-bottom: 103px;
    }
    
    .webinar-pastWebinars-title {
        width: 100%;
        height: 29%;
        
        display: flex;
        align-items: center;
        color: #30353E;
        
        font-size: 18px;
        font-family: 'Montserrat', sans-serif;
        color: #30353E;
    }
    
    .webinar-pastWebinars-table {
        width: 100%;
        height: 71%;
    }
    
    .webinar-pastWebinars-table-header {
        width: 100%;
        height: 23.1%;
        
        margin: 10px 0;
        
        font-family: 'Montserrat', sans-serif;
        font-size: 10px;
        color: #8F8F8F;
        
        display: flex;  
        align-items: center;
        flex-direction: row;
    }
    
    .webinar-pastWebinars-table-body {
        width: 100%;
        height: 76.9%;
    }
    
    .webinar-pastWebinars-table-header p:nth-child(1) {
        width: 12.55%;
    }
    
    .webinar-pastWebinars-table-header p:nth-child(2) {
        width: 6.84%;
        padding-left: 1.2%;
    }
    
    .webinar-pastWebinars-table-header p:nth-child(3) {
        width: 30.26%;
    }
    
    .webinar-pastWebinars-table-header p:nth-child(4) {
        width: 9.89%;
    }
    
    .webinar-pastWebinars-table-header p:nth-child(5) {
        width: 14.24%;
    }
    
    .webinar-pastWebinars-table-header p:nth-child(6) {
        width: 13.5%;
    }
    
    .webinar-pastWebinars-table-header p:nth-child(7) {
        width: 11.52%;
    }
    

</style>



