<template>
    <div class="settings_webinar-integration">
        <p class="settings_webinar-integration-title">Встраивание канала</p>
        <div class="settings_webinar-integration-container">
            
            <div class="settings_webinar-integration-container-in">
                
                <div class="settings_webinar-integration-container-in-left">
                    
                    <div class="settings_webinar-integration-container-in-left-item">
                        <div class="settings_webinar-integration-container-in-left-item-q">
                            <p>Server URL</p>
                            <Tooltip_question textMessage="URL предназначен для мониторинга прямого эфира, максимум 5 единовременных подключений"/>
                        </div>
                        <p class="settings_webinar-integration-container-in-left-item-url">rtmp://publish.facecast.net/publisher</p>
                        <div class="settings_webinar-integration-container-in-left-item-line"></div>
                    </div>
                    
                    <div class="settings_webinar-integration-container-in-left-item">
                        <div class="settings_webinar-integration-container-in-left-item-q">
                            <p>Stream Name</p>
                            <Tooltip_question textMessage="URL предназначен для мониторинга прямого эфира, максимум 5 единовременных подключений"/>
                        </div>
                        <p class="settings_webinar-integration-container-in-left-item-url">nastavnikme?sharedkey=8uksuxa4</p>
                        <div class="settings_webinar-integration-container-in-left-item-line"></div>
                    </div>
                    
                    <div class="settings_webinar-integration-container-in-left-item">
                        <div class="settings_webinar-integration-container-in-left-item-q">
                            <p>RTMP URL мониторинга прямого эфира</p>
                            <Tooltip_question textMessage="URL предназначен для мониторинга прямого эфира, максимум 5 единовременных подключений"/>
                        </div>
                        <p class="settings_webinar-integration-container-in-left-item-url">rtmp://publish.facecast.net/publisher/nastavnikme?sharedkey=8uksu</p>
                        <div class="settings_webinar-integration-container-in-left-item-line"></div>
                    </div>
                </div>
                
                <div class="settings_webinar-integration-container-in-right">
                    <p>Код для вставки канала в свой сайт</p>
                    <div class="settings_webinar-integration-container-in-right-code">
                        <p>&lt;style&gt;</p>
                        <p>#facecast_channel{}</p>
                        <p>#facecast_channel h1.channel_title{color:#333}</p>
                        <p>#empty_channel {</p>
                        <p>font-size:14px;</p>
                        <p>color:#777;</p>
                        <p>}</p>
                        <p>#facecast_channel .event{</p>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import Tooltip_question from '@/components/Tooltip_question.vue';
    
    export default {
        name: 'SettingsWebinar_integration',
        components: {
            Tooltip_question,
        }
    }
</script>

<style>
    .settings_webinar-integration {
        height: 368px;
        margin-top: 28px;
    }
    
    .settings_webinar-integration-title {
         width: 100%;
        height: 29px;
        
        font-size: 20px;
        font-weight: bold;
        color: #333333;
        
        display: flex;
        align-items: center;
        
        margin-bottom: 10px;
    }
    
    .settings_webinar-integration-container {
        width: 100%;
        height: 329px;
        border-radius: 10px;
        display: flex;
        
        background-color: #FFFFFF;
    }
    
    .settings_webinar-integration-container-in {
        margin-top: 43px;
        margin-left: 5.17%;
        width: 89.66%;
        height: 235px;
        
        display: flex;
        flex-direction: row;
    }
    
    .settings_webinar-integration-container-in-left{
        height: 100%;
        width: 52.7%;
        display: flex;
        flex-direction: column;
        
        
    }
    
    .settings_webinar-integration-container-in-left-item {
        height: 59px;
        width: 100%;
        margin-top: 28px;
    }
    
    .settings_webinar-integration-container-in-left .settings_webinar-integration-container-in-left-item:first-child {
        margin-top: 0px;
    }
    
    .settings_webinar-integration-container-in-left-item-q {
        width: 100%;
        height: 22px;
        
        display: flex;
        align-items: center;
        flex-direction: row;
    }
    
    .settings_webinar-integration-container-in-left-item-q p {
        margin-right: 13px;
        color: #30353E;
        font-weight: 600;
        font-size: 12px;
    }
    
    .settings_webinar-integration-container-in-left-item-url {
        margin-top: 6px;
        width: 100%;
        height: 22px;
        display: flex;
        align-items: center;
        
        font-size: 16px;
        color: #2C78F6;
    }
    
    .settings_webinar-integration-container-in-left-item-line {
        width: 88.16%;
        height: 1px;
        margin-top: 8px;
        background-color: #B8D2FC;
    }
    
    .settings_webinar-integration-container-in-right {
        height: 100%;
        width: 47.3%;
    }
    
    .settings_webinar-integration-container-in-right p {
        font-size: 14px;
        color: #B1B1B1;
        height: 22px;
        display: flex;
        align-items: center;
    }
    
    .settings_webinar-integration-container-in-right-code {
        margin-top: 6px;
        padding-top: 12px;
        padding-bottom: 16px;
        /*height: 177px;*/
        height: 89%;
        border: 1px solid #D2D2D2;
        border-radius: 10px;
        
        width: 92%;
        padding-left: 4%;
        padding-right: 4%;
    }
    
    .settings_webinar-integration-container-in-right-code p {
        font-size: 12px;
        color: #333333;
        line-height: 22px;
        font-weight: normal;
    }
</style>



